import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { TabPanel } from '@wordpress/components';
import { produce } from 'immer';

import { AboutPro } from '../../../../../Components/Pro';
import { tabController } from '../../../../../Components/utils/functions';

import General from './General';
import Elements from './Elements';
import Style from './Style';
import { generalStyleTabs } from '../../../utils/options';

const Settings = ({ isPremium, attributes, setAttributes, posts, taxOfPostType }) => {
	const { contentAlign } = attributes;

	const [aboutProOpen, setAboutProOpen] = useState(false);

	const isPosts = posts?.length;

	// Set max excerpt length
	// const [maxExcerptLength, setMaxExcerptLength] = useState(50);
	// useEffect(() => {
	// 	const allMaxExcerptLength = [];
	// 	posts?.map(post => {
	// 		allMaxExcerptLength.push(post?.excerpt?.replace(/(<p class='?"?read-more'?"?(.*?)<\/p>)/g, '').trim().split(' ').length);

	// 		setMaxExcerptLength(Math.max(...allMaxExcerptLength));
	// 	});
	// }, [posts]);

	const updateObj = (obj, key, val, childKey) => {
		const newObj = produce(attributes[obj], draft => {
			if (childKey) {
				draft[key][childKey] = val;
			} else {
				draft[key] = val;
			}
		});

		setAttributes({ [obj]: newObj });
	}

	const premiumProps = {
		isPremium,
		setOpen: setAboutProOpen
	};

	const props = {
		isPremium,
		attributes,
		setAttributes,
		premiumProps
	}

	return <>
		<InspectorControls>
			{!isPremium && <div className='apbInspectorInfo'>
				Need more block like this? Checkout the bundle  <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>}

			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <General {...props} />}

				{'elements' === tab.name && <Elements {...props} taxOfPostType={taxOfPostType} updateObj={updateObj} />}

				{'style' === tab.name && isPosts && <Style {...props} />}
			</>}</TabPanel>
		</InspectorControls>


		<AboutPro aboutProOpen={aboutProOpen} setAboutProOpen={setAboutProOpen} link='https://bplugins.com/products/advanced-post-block/#pricing'>
			<li>&emsp;<strong>{__('Layouts: ', 'advanced-post-block')}</strong>{__('Check out more layouts and sub-layouts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Display Pages: ', 'advanced-post-block')}</strong>{__('Effortlessly showcase pages, just like you do with posts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Tag Filter: ', 'advanced-post-block')}</strong>{__('Post filter by tags (only for default post).', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Custom Taxonomy Filter: ', 'advanced-post-block')}</strong>{__('Custom Taxonomy filter for post and custom posts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Post Offset: ', 'advanced-post-block')}</strong>{__('Offset to exclude first N posts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Include or Exclude Posts: ', 'advanced-post-block')}</strong>{__('Include or Exclude the posts by IDs.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Exclude Current Post: ', 'advanced-post-block')}</strong>{__('If you use this block in a post and that post type is selected post then you can exclude the post.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Pagination: ', 'advanced-post-block')}</strong>{__('Add custom pagination bottom of the posts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Sort Elements: ', 'advanced-post-block')}</strong>{__('Sort the title and meta elements.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Meta Data Icon: ', 'advanced-post-block')}</strong>{__('Custom icon for metadata.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Meta Author Link Toggle: ', 'advanced-post-block')}</strong>{__('Show meta author with or without link.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Taxonomies in Meta: ', 'advanced-post-block')}</strong>{__('Display taxonomies within the metadata., including tags.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Reading Time: ', 'advanced-post-block')}</strong>{__('Show post reading time in the meta area.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Excerpt from Content: ', 'advanced-post-block')}</strong>{__('Show excerpt from main content.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Ticker Layout Options: ', 'advanced-post-block')}</strong>{__('Options for ticker post layout.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Feature Image Size: ', 'advanced-post-block')}</strong>{__('Options to set feature image size.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Read More Position: ', 'advanced-post-block')}</strong>{__('Options to set the position for the Read More button.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('ShortCode Powered: ', 'advanced-post-block')}</strong>{__('Shortcode option to use anywhere.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Update Post Query: ', 'advanced-post-block')}</strong>{__('Update Post Query using apb_query filter.', 'advanced-post-block')}</li>
		</AboutPro>


		<BlockControls>
			<AlignmentToolbar value={contentAlign} onChange={val => setAttributes({ contentAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;