import { useEffect } from 'react';

import { useWPAjax, useWPOptionQuery } from '../../../react-utils/hooks';

const usePremiumInEditor = () => {
	const { data: apbUtils } = useWPOptionQuery('apbUtils');
	const { data = null, refetch, isLoading = true } = useWPAjax('apbPipeChecker', { _wpnonce: apbUtils?.nonce }, true);
	const isPremium = (!isLoading && data?.isPipe) || false;

	useEffect(() => {
		refetch();
	}, [apbUtils]);

	return { isPremium, isLoading };
};
export default usePremiumInEditor;